:root
{
    --primaryColor: rgb(86 108 224);
    --darkColor: rgb(0, 0, 0);
    --toggleToDark: rgb(194, 190, 190);
    --toggleToLight: rgb(184, 194, 245);
}
*
{
    margin: 1px;
    padding: 0;
    box-sizing: border-box;
}


/* *****************LightMode */
.navbar
{
    background: var(--primaryColor);
    box-shadow: -1px -1px 2px 0px var(--primaryColor), 1px 1px 2px 0px var(--primaryColor);
    display: flex;
    justify-content: space-between;
    border: 4px solid var(--primaryColor);
    padding: 5px;
    margin-bottom: 4px;
    margin-top: 1px;
    border-bottom-left-radius: 40px;
    border-top-right-radius: 40px;
}

.navbar a
{
    text-decoration: none;
    font-size: 30px;
    font-weight: bold;
    color: white;
}
.navbar #toggle
{
    border-radius: 10px;
    font-size: 30px;
    color: white;
    background: var(--primaryColor);
    border: 0px;
    cursor: pointer;
    font-weight: bold;

}
.navbar p
{
    color: white;
    font-size: 33px;
    font-weight: bold;
}
.navbar #toggle:hover
{
    color: var(--toggleToDark);
}
.navbar p:hover
{
    color: red;
}

/* *****************DarkMode */
.navbarDark
{
    background: var(--darkColor);
    box-shadow: -1px -1px 2px 0px var(--darkColor), 1px 1px 2px 0px var(--darkColor);
    display: flex;
    justify-content: space-between;
    border: 4px solid var(--darkColor);
    padding: 5px;
    margin-bottom: 4px;
    margin-top: 1px;
    border-bottom-left-radius: 40px;
    border-top-right-radius: 40px;
}
.navbarDark a
{
    text-decoration: none;
    font-size: 30px;
    font-weight: bold;
    color: white;
}
.navbarDark #toggle
{
    border-radius: 10px;
    font-size: 30px;
    color: white;
    background: black;
    border: 0px;
    cursor: pointer;
    font-weight: bold;
}
.navbarDark p
{
    color: white;
    font-size: 33px;
    font-weight: bold;
}
.navbarDark #toggle:hover
{
    color: var(--toggleToLight);
}
.navbarDark p:hover
{
    color: red;
}
