:root
{
    --Body: #d7d6d6;
    --primaryColor: rgb(86 108 224);
    --darkColor: rgb(0, 0, 0);
}
*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box
}

/* ***********LightMode */
.buttons
{
    cursor: pointer;
    padding: 5px;
    font-size: 17px;
    font-weight: bolder;
    border: 3px solid var(--primaryColor);
    background: white;
    color: var(--primaryColor);
}

.container
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 5px solid var(--primaryColor);
}
.container textarea
{
    border: 3px solid var(--primaryColor);
    border-radius: 5px;
    box-shadow: 0px 0px 0px 0px red;

    transition: box-shadow 10s ease 0s, text-shadow 3s ease 0s, transform 10s ease 0s;
}
#heading
{
    color: var(--primaryColor);
}
#summary
{
    color: var(--primaryColor);
    transition: font-size 6s ease 0s;
}

.buttonDiv
{
    display: flex;
    width: 25%;
    justify-content: space-between;
    margin-top: 5px;
    /* border: 2px solid red; */
}

.buttonDiv .clear
{
    color: red;

}

/* ***********DarkMode */
.buttonsDark
{
    cursor: pointer;
    padding: 5px;
    font-size: 17px;
    font-weight: bolder;
    border: 3px solid white;
    background: var(--darkColor);
    color: white;
}
.containerDark
{

    background: var(--darkColor);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 5px solid var(--darkColor);
}
.containerDark textarea
{
    border: 4px solid white;
    border-radius: 5px;
    box-shadow: 0px 0px 0px 0px red;

    background: var(--darkColor);
    color: white;

    transition: box-shadow 10s ease 0s, text-shadow 3s ease 0s, transform 10s ease 0s;
}
#headingDark
{
    color: white;
}
#summaryDark
{
    color: white;
    transition: font-size 6s ease 0s;
}

.buttonDivDark
{
    display: flex;
    width: 25%;
    justify-content: space-between;
    margin-top: 5px;
    /* border: 2px solid red; */
}

.buttonDivDark .clear
{
    color: red;
}



